import { Wrapper, Titulo, Link } from "../UseState&props/styles"
import { useToggleDisplayState } from "../../context/useToggleDisplay";
import { Button } from "../Button"
import styled from 'styled-components';


const P = styled.p`
    color: white;
    width: 80%;
    max-width: 600px;
    margin-bottom: 25px;
`;

export const UseContext = function () {
    const {display, setDisplay} = useToggleDisplayState();
    return(
        <Wrapper style={display ? {backgroundColor: "#000000"} : {backgroundColor: "#000000"}}>
            <Link id="secao6"/>
            <Titulo>UseContext</Titulo>
            <P>O context  uma soluo para o problema de ficar passando props de pai pra filho infinitamente pra chegar no componente que voc precisa usar.</P>
            <P>Perceba que ao clicar no boto abaixo, todos os componentes do site so alterados, sendo que  alterado o componente Card, que  filho direto da seo anterior</P>
            <Button style={{backgroundColor: "#000000"}} onClick={() => setDisplay(!display)}>Alterar contexto</Button>
        </Wrapper>
    )
}